.popup__container {
  width: auto;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  position: relative;
  color: #000;
  box-sizing: border-box;
}

@media screen and (max-width: 580px) {
  .popup__container {
    max-width: 282px;
  }
}
