.photo-grid {
  max-width: 880px;
  width: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px 17px;
}

@media screen and (max-width: 1024px) {
  .photo-grid {
    max-width: 581px;
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 630px) {
  .photo-grid {
    max-width: 282px;
    grid-template-columns: 1fr;
  }
}

